<template>
   <div class='roles'>
       <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>权限管理</el-breadcrumb-item>
            <el-breadcrumb-item>角色列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card> 
            <!-- 添加角色 -->
            <el-row :gutter="20">
                <el-col :span="6">
                     <el-button type="primary" @click="dialogVisible=true">添加角色</el-button>
                </el-col>
            </el-row>
            <!-- 列表 -->
             <el-table
                :data="list"
                border stripe
                style="width: 100%">
                <!-- 展开列 -->
                <el-table-column type="expand" width="40">
                  <template slot-scope="scope">
                      <el-row :class="['bdbottom',i1 === 0 ? 'bdtop' :'','el-col1']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                        <!-- 渲染一级权限 -->
                          <el-col class="el-col1" :span="4">
                             <el-tag closable @close="scqx(scope.row,item1.id)">{{item1.authName}}</el-tag><i class="el-icon-caret-right"></i>
                          </el-col>
                          <!-- 渲染二级权限和三级权限 -->
                          <el-col :span="19">
                              <!-- 通过for循环嵌套渲染二级权限 -->
                              <el-row :class="[i2 === 0 ? '':'bdtop','el-col1']" v-for="(item2,i2) in item1.children" :key="item2.id">
                                  <el-col :span="5">
                                      <el-tag closable @close="scqx(scope.row,item2.id)" type="success">{{item2.authName}}</el-tag><i class="el-icon-caret-right"></i>
                                  </el-col>
                                  <el-col :span="18">
                                       <el-tag closable @close="scqx(scope.row,item3.id)" v-for="(item3) in item2.children" :key="item3.id"  type="warning">{{item3.authName}}</el-tag>                                         
                                  </el-col>
                              </el-row>
                          </el-col>
                      </el-row>
                      <!-- <pre>
                          {{scope.row}}
                      </pre> -->
                  </template>
                </el-table-column>
                <el-table-column
                label="#"
                   type ="index">
                </el-table-column>
                <el-table-column
                    prop="roleName"
                    label="角色名称"
                    >
                </el-table-column>
                 <el-table-column
                    prop="roleDesc"
                    label="角色描述"
                  >
                </el-table-column>
                <el-table-column label="操作" width="300">
                    <template slot-scope="scope">
                             <el-tooltip effect="dark" content="修改按钮" placement="top" :enterable="false">
                            <el-button  @click="xiugai(scope.row)" type="primary" size="mini" icon="el-icon-edit" >修改</el-button>
                            </el-tooltip>
                             <el-tooltip  effect="dark" content="删除按钮" placement="top" :enterable="false">
                            <el-button @click="shanchu(scope.row)" type="danger" size="mini" icon="el-icon-delete" >删除</el-button>
                            </el-tooltip>
                            <el-tooltip  effect="dark" content="分配权限按钮" placement="top" :enterable="false">
                                <el-button @click="fenpeiqx(scope.row)" type="warning" size="mini" icon="el-icon-setting">分配权限</el-button>
                            </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!-- 添加角色弹框 -->
         <el-dialog
            title="添加角色"
            :visible.sync="dialogVisible"
            width="60%">
            <!-- 内容主体区 -->
            <el-form :model="addFrom" ref="addref" label-width="70px">
                <el-form-item  label="角色名称" prop="roleName">
                    <el-input v-model="addFrom.roleName"></el-input>
                </el-form-item>
                <el-form-item  label="角色描述" prop="roleDesc">
                    <el-input v-model="addFrom.roleDesc"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部按钮区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible=false">取 消</el-button>
                <el-button type="primary" @click="addroles">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 修改角色的弹框 -->
        <el-dialog
            title="添加角色"
            :visible.sync="dialogVisible1"
            width="60%">
            <!-- 内容主体区 -->
            <el-form :model="xiugaiFrom" ref="addrefs" label-width="70px">
                <el-form-item  label="角色名称" prop="roleName">
                    <el-input v-model="xiugaiFrom.roleName"></el-input>
                </el-form-item>
                <el-form-item  label="角色描述" prop="roleDesc">
                    <el-input v-model="xiugaiFrom.roleDesc"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部按钮区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1=false">取 消</el-button>
                <el-button type="primary" @click="xiugaiqd">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 分配权限弹框 -->
        <el-dialog
            title="分配权限"
            :visible.sync="dialogVisible2"
            @close="gbfp"
            width="60%">
            <!-- 内容主体区 -->
           <el-tree :data="list1" 
           ref="treeref"
           show-checkbox 
           node-key='id' 
           default-expand-all
           :default-checked-keys ="defkeys"
           :props="treeProps" >
            </el-tree>
            <!-- 底部按钮区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2=false">取 消</el-button>
                <el-button type="primary" @click="qdfptk">确 定</el-button>
            </span>
        </el-dialog>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
           list:[],
           dialogVisible:false,
            dialogVisible1:false,
             dialogVisible2:false,
           //添加
            addFrom:{
                roleName:"",
                roleDesc:""
            },
            //修改
            xiugaiFrom:{
                 roleName:"",
                roleDesc:"",
               roleId:""
            },
            //权限树的数据
            list1:[],
             treeProps: {
                children: 'children',
                label:'authName'
             },
             //默认选中的值
             defkeys:[],
             //分配框的id
             fpid:""
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
       //请求页面数据
     async getroleslist(){
          let {data:res} = await this.$http.get('roles')
          this.list = res.data
          console.log(this.list)
      },
      //添加角色
      async addroles(){
          let { data:res } = await this.$http.post(`roles`,this.addFrom)
        //   console.log(res)
          if(res.meta.status == 201){
               this.$message({
                message: res.meta.msg,
                type: 'success'
                });

            }else{
                this.$message({
                message:"添加失败",
                type: 'warning'
                });
            }
            //再次调用页面数据
             this.getroleslist()
             this.dialogVisible = false

      },
      //删除角色权限
   async scqx(role,rightId){
         let conshanchu= await this.$confirm('此操作将永久删除该用户权限, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err =>{
            return err
        })
         //如果不是字符串confirm，那么就是取消删除
        if(conshanchu !== "confirm"){
            return this.$message.info("已经取消删除")
        }
        //如果点击确定，就删除
        let { data:res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        console.log(res)
        if(res.meta.status == 200){
            //取消成功  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });   
        }else{
            //取消失败
            this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
        }
        role.children = res.data
       
    },
    //点击修改显示弹框
   async xiugai(v){
        console.log(v)
         this.dialogVisible1 = true
         let {data:res} = await this.$http.get(`roles/${ v.id }`)
         console.log(res)
         this.xiugaiFrom.roleName = res.data.roleName
          this.xiugaiFrom.roleDesc = res.data.roleDesc
          this.xiugaiFrom.roleId = res.data.roleId
    },
    //点击修改显示弹框里的确定
    xiugaiqd(){
          this.$refs.addrefs.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //可以发起添加用户的请求
              let { data:res } = await this.$http.put(`roles/${this.xiugaiFrom.roleId}`,this.xiugaiFrom)
                    // console.log(res) 
                    if(res.meta.status == 200){
                        //修改成功  
                        this.$message({
                            message: "修改成功",
                            type: 'success'
                            });   
                    }else{
                        //修改失败
                        this.$message({
                            message: "修改失败",
                            type: 'warning'
                            });
                    }
                    this.dialogVisible1 = false
                    //再次调用列表数据
                   this.getroleslist()
            })
    },
    //删除角色
        async shanchu(v){
            console.log(v)
                 let conshanchu= await this.$confirm('此操作将永久删除该用户权限, 是否继续?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).catch(err =>{
                    return err
                })
                 //如果不是字符串confirm，那么就是取消删除
                if(conshanchu !== "confirm"){
                    return this.$message.info("已经取消删除")
                }
                //如果点击确定，就删除
                let { data:res } = await this.$http.delete(`roles/${v.id}`)
                console.log(res)
                if(res.meta.status == 200){
                    //取消成功  
                    this.$message({
                        message: res.meta.msg,
                        type: 'success'
                        });   
                }else{
                    //取消失败
                    this.$message({
                        message: res.meta.msg,
                        type: 'warning'
                        });
                }
                 this.getroleslist()
            },
            //分配权限弹框
            async fenpeiqx(v){
                //获取分配权限的id
                this.fpid = v.id
               let { data:res }= await this.$http.get('rights/tree')
               if(res.meta.status !== 200){
                   return this.$message.error("获取失败")
               }
                this.list1 = res.data
                console.log(this.list1)
                this.getkeys(v,this.defkeys)
                this.dialogVisible2 = true
            },
            //打开弹框已有的权限默认选中
            getkeys(node,arr){
                if(!node.children){
                    return arr.push(node.id)
                }
                node.children.forEach(item =>this.getkeys(item,arr))
            },
            //关闭分配弹框
            gbfp(){
                this.defkeys = []
            },
            //点击分配弹框的确定按钮
           async qdfptk(){
               let keys = [
                   ...this.$refs.treeref.getCheckedKeys(),
                   ...this.$refs.treeref.getHalfCheckedNodes()
               ]
               let keys2 = keys.join(',')
               let { data:res } = await this.$http.post(`roles/${this.fpid}/rights`,{rids:keys2})
                console.log(res)
                if(res.meta.status == 200){
                    //授权成功  
                    this.$message({
                        message: res.meta.msg,
                        type: 'success'
                        });   
                }else{
                    //授权失败
                    this.$message({
                        message: res.meta.msg,
                        type: 'warning'
                        });
                }
                this.dialogVisible2 = false
                this.getroleslist()
            }

   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
       this.getroleslist()
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style  lang='less' scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.el-table{
    margin-top: 20px;
}
.el-tag{
    margin: 20px;
}
.bdbottom{
    border-bottom: 1px solid #eee;
}
.bdtop{
    border-top: 1px solid #eee;
}
.el-col1{
 display: flex;
 align-items: center;
}
</style>