<template>
   <div class='categories'>
       <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品分类</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 内容 -->
        <el-card class="box-card">
             <!-- 添加分类 -->
            <el-row :gutter="20">
                <el-col :span="6">
                     <el-button type="primary" @click="tjfl">添加分类</el-button>
                </el-col>
            </el-row>
            <!-- 列表 -->
            <tree-table
            class="tree-table1"
             :data="list"
             :columns="columns"
             :selection-type="false"
             :expand-type="false"
             :show-index="true"
             index-text="#"
             :border="true"
             :show-row-hover="true"
            >
                <template slot="isok" slot-scope="scope">
                        <i class="el-icon-success"  style="color:lightgreen;fontSize:18px" v-if="!scope.row.cat_deleted"></i>
                         <i class="el-icon-success"  style="color:#ccc;fontSize:18px" v-else></i>
                </template>
                 <template slot="paixv" slot-scope="scope">
                        <el-tag v-if="scope.row.cat_level == 0">{{ scope.row.cat_level | gl}}</el-tag>
                        <el-tag  v-if="scope.row.cat_level == 1" type="success">{{ scope.row.cat_level | gl}}</el-tag>
                        <el-tag  v-if="scope.row.cat_level == 2" type="warning">{{ scope.row.cat_level | gl}}</el-tag>
                </template>
                 <template slot="cz" slot-scope="scope">
                             <el-tooltip  effect="dark" content="修改按钮" placement="top" :enterable="false">
                            <el-button type="primary" @click="xiugai(scope.row)" size="mini" icon="el-icon-edit" >修改</el-button>
                            </el-tooltip>
                             <el-tooltip  effect="dark" content="删除按钮" placement="top" :enterable="false">
                            <el-button type="danger" @click="shanchu(scope.row)" size="mini" icon="el-icon-delete" >删除</el-button>
                            </el-tooltip>
                </template> 
            </tree-table>
             <!-- 分页 -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </el-card>
         <!-- 修改角色的弹框 -->
        <el-dialog
            title="修改分类"
            :visible.sync="dialogVisible"
            @close="adddialog"
            width="60%">
            <!-- 内容主体区 -->
            <el-form :model="xiugaiFrom" ref="addrefs" label-width="70px">
                <el-form-item  label="分类名称" prop="cat_name">
                    <el-input v-model="xiugaiFrom.cat_name"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部按钮区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible=false">取 消</el-button>
                <el-button type="primary"  @click="xiugaiqd">确 定</el-button>
            </span>
        </el-dialog>
         <!-- 添加的弹框 -->
        <el-dialog
            title="添加用户"
            :visible.sync="dialogVisible1"
             @close="adddialog1"
            width="60%">
            <!-- 内容主体区 -->
            <el-form :model="addFrom" :rules="addRules"  ref="addref" label-width="100px">
                 <el-form-item  label="分类名称:" prop="cat_name">
                    <el-input v-model="addFrom.cat_name"></el-input>
                </el-form-item>
                <el-form-item label="父级分类:" >
                    <el-cascader
                    v-model="flcat_pid"
                    :options="list2"
                    :props="cascaderProps"
                    expand-trigger= "hover"
                    :clearable="true"
                     :change-on-select="true"
                     @change="flhandleChange"
                    ></el-cascader>
                </el-form-item>
            </el-form>
            <!-- 底部按钮区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1=false">取 消</el-button>
                <el-button type="primary" @click="fltjqd">确 定</el-button>
            </span>
        </el-dialog>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
           dialogVisible:false,
           dialogVisible1:false,
            //修改
            xiugaiFrom:{
              
            },
            flcat_pid:[],
             //添加表单数据
             addFrom:{
                 cat_pid:0,
                cat_name:"",
                cat_level:0
             },
           list:[],
           list2:[],
           queryInfo:{
               query:"",
               //当前的页数
               pagenum:1,
               //当前每页显示多少条数据
               pagesize:7
           },
            total:0,
            columns:[
                {
                    label: '商品名称',
                    prop:"cat_name",
                },
                {
                    label: '是否有效',
                    //表示将当前列定义为模板列
                    type: 'template',
                    //模板字列的名字
                    template: 'isok',
                    width: '140px'
                },
                {
                    label: '排序',
                    //表示将当前列定义为模板列
                    type: 'template',
                    //模板字列的名字
                    template: 'paixv',
                },
                {
                    label: '操作',
                    //表示将当前列定义为模板列
                    type: 'template',
                    //模板字列的名字
                    template: 'cz',
                },
            ],
             //表单验证
             addRules:{
                 //分类名称
                  cat_name:[
                        { required: true, message: '分类名称', trigger: 'blur' },
                        { min: 1, max: 5, message: '长度在 1 到 5 个字符', trigger: 'blur' }
                    ],
             },
             //指定级联选择器的配置对象
              cascaderProps:{
                value:"cat_id",
                label:"cat_name",
                children:"children"
            }

       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   filters:{
      gl(val){
         if(val == "0"){
            return "一级"
         }else if(val == "1"){
             return "二级"
         }else if(val == "2"){
             return "三级"
         }
      }
   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
       //获取数据
       async getcatlist(){
           let { data:res} = await this.$http.get(`categories`,{params:this.queryInfo})
            if(res.meta.status !== 200){
                return this.$message.error("获取商品分类数据失败")
            }
           this.list = res.data.result
             this.total = res.data.total
           console.log(res)
       },
       //获取父级分类数据
        async getfjfllist(){
            let { data:res} = await this.$http.get(`categories`,{params:
            {type:2}})
            if(res.meta.status !== 200){
                return this.$message.error("获取商品父级分类数据失败")
            }
            this.list2 = res.data
            console.log(this.list2)
        },
       //分页
       //监听pagesize改变的事件
       handleSizeChange(newsize){
           console.log(newsize)
           //切换每页显示条数
           this.queryInfo.pagesize = newsize
         this.getcatlist()
       },
       //监听页码值 改变的事件
       handleCurrentChange(newpage){
           console.log(newpage)
        //    切换页数
           this.queryInfo.pagenum = newpage
            this.getcatlist()
       },
       //修改表单重置
       adddialog(){

           this.$refs.addrefs.resetFields()
       },
       //添加表单重置
       adddialog1(){
           this.$refs.addref.resetFields()
           this.flcat_pid = []
           this.addFrom.cat_level = 0
       },
        //点击修改显示弹框
        async xiugai(v){
                this.dialogVisible = true
                let {data:res} = await this.$http.get(`categories/${ v.cat_id }`)
                // console.log(res)
                this.xiugaiFrom = res.data
                console.log(this.xiugaiFrom)
            },
             //修改确定
        xiugaiqd(){
            this.$refs.addrefs.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //可以发起添加用户的请求
              let { data:res } = await this.$http.put(`categories/${this.xiugaiFrom.cat_id}`,this.xiugaiFrom)
                    // console.log(res) 
                    if(res.meta.status == 200){
                        //修改成功  
                        this.$message({
                            message: res.meta.msg,
                            type: 'success'
                            });   
                    }else{
                        //修改失败
                        this.$message({
                            message: res.meta.msg,
                            type: 'warning'
                            });
                    }
                    this.dialogVisible = false
                    //再次调用列表数据
                   this.getcatlist()
            })
        },
         //删除按钮
       async shanchu(v) {
            console.log(v)
        let conshanchu= await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err =>{
            return err
        })
        //如果不是字符串confirm，那么就是取消删除
        if(conshanchu !== "confirm"){
            return this.$message.info("已经取消删除")
        }
        //如果点击确定，就删除
        let { data:res } = await this.$http.delete('categories/'+v.cat_id)
        console.log(res)
        if(res.meta.status == 200){
            //删除成功  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });   
        }else{
            //删除失败
            this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
        }
        //再次调用列表数据
       this.getcatlist()
      },
        //点击添加分类
        tjfl(){
            //获取父级分类数据
              this.getfjfllist()
                 this.dialogVisible1 = true
        },
        //选中的父级分类id
        flhandleChange(){
            console.log(this.flcat_pid)
            if(this.flcat_pid.length >0){
                this.addFrom.cat_pid=this.flcat_pid[this.flcat_pid.length-1]
               this.addFrom.cat_level = this.flcat_pid.length
               return
            }else{
                 this.addFrom.cat_pid=0
               this.addFrom.cat_level = 0
            }

        },
        //点击添加确定
        fltjqd(){
            this.$refs.addref.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //可以发起添加用户的请求
              let { data:res } = await this.$http.post(`categories`,this.addFrom)
                    console.log(res) 
                    if(res.meta.status == 201){
                        //添加成功  
                        this.$message({
                            message: res.meta.msg,
                            type: 'success'
                            });   
                    }else{
                        //添加失败
                        this.$message({
                            message: res.meta.msg,
                            type: 'warning'
                            });
                    }
                    this.getcatlist()
                    this.dialogVisible1 = false
                     
            })
        }
       
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
       this.getcatlist()
     
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.tree-table1{
    margin-top: 20px;
}
.el-pagination{
    margin-top: 20px;
}
.el-form-item__label{
 font-size: 12px;
}
.el-cascader{
    width: 100%;
}
</style>