<template>
   <div class='params'>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>分类参数</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 内容 -->
        <el-card>
            <!-- 提示消息 -->
            <el-alert
                title="注意：只允许为第三级分类设置相关参数！"
                type="warning"
                show-icon
                :closable="false">
            </el-alert>
            <!-- 选择商品分类 -->
            <el-row :gutter="20">
                <el-col :span="6">
                     <el-form :model="addFrom"  ref="addref" label-width="100px">
                        <el-form-item label="选择商品分类:" >
                            <el-cascader
                            v-model="flcat_pid"
                            :options="list2"
                            :props="cascaderProps"
                            expand-trigger= "hover"
                            :clearable="true"
                            @change="flhandleChange"
                            ></el-cascader>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
            <!-- 导航 -->
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1">动态参数</el-menu-item> 
                <el-menu-item index="2">静态属性</el-menu-item>
            </el-menu>
            <div v-if="activeIndex == '1'">
                 <!-- 添加参数 -->
                <el-row :gutter="20">
                    <el-col :span="6">
                        <el-button size = "mini" type="primary" >添加参数</el-button>
                    </el-col>
                </el-row>
                     <!-- 列表 -->
                    <el-table
                    :data="list"
                    border stripe
                    style="width: 100%">
                    <!-- 展开列 -->
                    <el-table-column type="expand" width="40">
                        <template slot-scope="scope">
                            <pre>
                                {{scope.row}}
                            </pre>
                        </template>
                    </el-table-column>
                    <el-table-column
                    label="#"
                        type ="index">
                    </el-table-column>
                    <el-table-column
                        prop="roleName"
                        label="参数名称"
                        >
                    </el-table-column>
                    <el-table-column label="操作" width="500">
                        <template slot-scope="scope">
                                    <el-tooltip effect="dark" content="修改按钮" placement="top" :enterable="false">
                                <el-button   type="primary" size="mini" icon="el-icon-edit" >修改</el-button>
                                </el-tooltip>
                                    <el-tooltip  effect="dark" content="删除按钮" placement="top" :enterable="false">
                                <el-button type="danger" size="mini" icon="el-icon-delete" >删除</el-button>
                                </el-tooltip>
                        </template>
                    </el-table-column>
                </el-table>
                </div>
                <div  v-if="activeIndex == '2'">
                        <!-- 添加属性 -->
                    <el-row :gutter="20">
                        <el-col :span="6">
                            <el-button size = "mini" type="primary" >添加属性</el-button>
                        </el-col>
                    </el-row>
                     <!-- 列表 -->
                    <el-table
                    :data="list"
                    border stripe
                    style="width: 100%">
                    <!-- 展开列 -->
                    <el-table-column type="expand" width="40">
                        <template slot-scope="scope">
                            <pre>
                                {{scope.row}}
                            </pre>
                        </template>
                    </el-table-column>
                    <el-table-column
                    label="#"
                        type ="index">
                    </el-table-column>
                    <el-table-column
                        prop="roleName"
                        label="参数名称"
                        >
                    </el-table-column>
                    <el-table-column label="操作" width="500">
                        <template slot-scope="scope">
                                    <el-tooltip effect="dark" content="修改按钮" placement="top" :enterable="false">
                                <el-button   type="primary" size="mini" icon="el-icon-edit" >修改</el-button>
                                </el-tooltip>
                                    <el-tooltip  effect="dark" content="删除按钮" placement="top" :enterable="false">
                                <el-button type="danger" size="mini" icon="el-icon-delete" >删除</el-button>
                                </el-tooltip>
                        </template>
                    </el-table-column>
                </el-table>
