                children:"children"
            },
            list:[],
            
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
        //获取父级分类数据
        async getfjfllist(){
            let { data:res} = await this.$http.get(`categories`,{params:
            {type:3}})
            if(res.meta.status !== 200){
                return this.$message.error("获取商品父级分类数据失败")
            }
            this.list2 = res.data
            console.log(this.list2)
        },
       async flhandleChange(v){
            // console.log(v)
            // console.log(v[2])
            // console.log(this.sel)
            // let { data:res} = await this.$http.get(`categories/${v[2]}/attributes`,{sel:this.sel})
            // console.log(res)
        },
        handleSelect(key, keyPath) {
        console.log(key, keyPath);
            this.activeIndex = key
            // if(key == 1){
            //     this.sel = only
            // }else if(key == 2){
            //      this.sel = many
            // }
      },
      

   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
       this.getfjfllist()
   
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang="less" scoped>
.el-card{
    margin-top: 20px;
}
.el-cascader{
    width: 250px;
}
.el-row{
    margin-top: 20px;
}
</style>