<template>
   <div class='goods'>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <!-- 搜索和添加 -->
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-input clearable @clear='getfllist'  v-model="queryInfo.query" clearable placeholder="请输入内容" class="input-with-select">
                        <el-button @click="getfllist" slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="6">
                     <el-button type="primary" @click="tz">添加用户</el-button>
                </el-col>
            </el-row>
            <!-- 列表 -->
             <el-table
                :data="list"
                border stripe
                style="width: 100%">
                <el-table-column
                    label="#"
                   type ="index">
                </el-table-column>
                <el-table-column
                    prop="goods_name"
                    label="商品名称"
                    width="520">
                </el-table-column>
                 <el-table-column
                    prop="goods_price"
                    label="商品价格">
                </el-table-column>
                 <el-table-column
                    prop="goods_weight"
                    label="商品重量">
                </el-table-column>
                <el-table-column  
                    label="创建时间"
                     width="160">
                     <template slot-scope="scope">
                            {{ scope.row.add_time | shijian}}
                     </template>
                </el-table-column>
                <el-table-column label="操作"  width="160">
                    <template slot-scope="scope">
                             <el-tooltip  effect="dark" content="修改按钮" placement="top" :enterable="false">
                            <el-button type="primary" size="mini" icon="el-icon-edit" ></el-button>
                            </el-tooltip>
                             <el-tooltip  effect="dark" content="删除按钮" placement="top" :enterable="false">
                            <el-button @click="shanchu(scope.row)" type="danger" size="mini" icon="el-icon-delete" ></el-button>
                            </el-tooltip>
                    </template> 
                </el-table-column>
            </el-table>
             <!-- 分页 -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 5, 10, 20]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </el-card>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
           list:[],
           queryInfo:{
               query:"",
               //当前的页数
               pagenum:1,
               //当前每页显示多少条数据
               pagesize:10
           },
            total:0,

       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   filters:{
       shijian(time){
           let date   = new Date(time)//把定义的时间赋值进来进行下面的转换
        let year   = date.getFullYear();
        let month  = date.getMonth()+1;
        let day    = date.getDate();
        let hour   = date.getHours(); 
        let minute = date.getMinutes(); 
        let second = date.getSeconds(); 
        return year+"-"+month+"-"+day+" "+hour+":"+minute+":"+second;
       }
   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
       //跳转添加页面
       tz(){
           this.$router.push('/goods/add')
       },
    //    获取数据
      async getfllist(){
          let { data:res } = await this.$http.get(`goods`,{params:this.queryInfo})
           if(res.meta.status !== 200){
           return this.$message.error("获取数据失败")
       }
          this.list = res.data.goods
            this.total = res.data.total
          console.log(res)
       },
       //分页
       //监听pagesize改变的事件
       handleSizeChange(newsize){
           console.log(newsize)
           //切换每页显示条数
           this.queryInfo.pagesize = newsize
         this.getfllist()
       },
       //监听页码值 改变的事件
       handleCurrentChange(newpage){
           console.log(newpage)
        //    切换页数
           this.queryInfo.pagenum = newpage
           this.getfllist()
       },
        //删除按钮
       async shanchu(v) {
            // console.log(v.goods_id)
        let conshanchu= await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err =>{
            return err
        })
        //如果不是字符串confirm，那么就是取消删除
        if(conshanchu !== "confirm"){
            return this.$message.info("已经取消删除")
        }
        //如果点击确定，就删除
        let { data:res } = await this.$http.delete('goods/'+v.goods_id)
        console.log(res)
        if(res.meta.status == 200){
            //删除成功  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });   
        }else{
            //删除失败
            this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
        }
        //再次调用列表数据
        this.getfllist()
      },
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
       this.getfllist()
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.el-table{
    margin-top: 20px;
}
.el-pagination{
    margin-top: 20px;
}

</style>