<template>
  <div class="order">
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="请输入内容" v-model="queryInfo.query">
            <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!-- 订单列表数据 -->
      <el-table :data="orderList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="订单编号" prop="order_number"></el-table-column>
        <el-table-column label="订单价格" prop="order_price"></el-table-column>
        <el-table-column label="是否付款" prop="pay_status">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'">已付款</el-tag>
            <el-tag type="danger" v-else-if="scope.row.pay_status === '0'">未付款</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="是否发货" prop="is_send"></el-table-column>
        <el-table-column label="下单时间" prop="create_time">
          <template slot-scope="scope">{{ scope.row.create_time | shijian }}</template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showOrder"></el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="showOrderBox"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 8, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- 省市区的对话框 -->
    <el-dialog title="修改地址" :visible.sync="orderVisible" width="50%">
      <el-form
        :model="addOrderForm"
        :rules="addOrderRules"
        ref="addOrderRuleForm"
        label-width="100px"
      >
        <el-form-item label="省市区/县" prop="addOrder1">
          <el-cascader v-model="addOrderForm.addOrder1"></el-cascader>
        </el-form-item>
        <el-form-item label="详细地址" prop="addOrder2">
          <el-input v-model="addOrderForm.addOrder2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="orderVisible = false">取 消</el-button>
        <el-button type="primary" @click="orderVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 物流进度的对话框 -->
    <el-dialog title="物流进度" :visible.sync="wuliuVisible" width="50%">
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in wuliuList"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="wuliuVisible = false">取 消</el-button>
        <el-button type="primary" @click="wuliuVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// import area from "./area";
export default {
  data() {
    return {
      // 查询对象
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      // 订单列表数据
      orderList: [],
      // 总数据条数
      total: 0,
      //省市区的对话框的显示与隐藏
      orderVisible: false,
      addOrderForm: {
        addOrder1: [],
        addOrder2: ""
      },
      addOrderRules: {
        addOrder1: [
          { required: true, message: "请选择省市区/县", trigger: "blur" }
        ],
        addOrder2: [
          { required: true, message: "请填写详细地址", trigger: "blur" }
        ]
      },
      wuliuVisible: false,
      wuliuList: []
    };
  },
  filters:{
       shijian(time){
           let date   = new Date(time)//把定义的时间赋值进来进行下面的转换
        let year   = date.getFullYear();
        let month  = date.getMonth()+1;
        let day    = date.getDate();
        let hour   = date.getHours(); 
        let minute = date.getMinutes(); 
        let second = date.getSeconds(); 
        return year+"-"+month+"-"+day+" "+hour+":"+minute+":"+second;
       }
   },
  created() {
    // 调用订单列表数据
    this.getOrderList();
  },
  methods: {
    // 订单列表数据
    async getOrderList() {
      let { data: res } = await this.$http.get("orders", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) {
        this.$message.error("获取订单数据失败！");
      } else {
        this.orderList = res.data.goods;
        this.total = res.data.total;
      }
    },
    // 分页的条数
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      // 调用订单列表数据
      this.getOrderList();
    },
    // 分页的页数
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      // 调用订单列表数据
      this.getOrderList();
    },
    // 省市区的对话框的显示与隐藏
    showOrder() {
      this.orderVisible = true;
    },
    // 物流进度的点击事件
    async showOrderBox() {
      let { data: res } = await this.$http.get("/kuaidi/1106975712662");
      if (res.meta.status !== 200) {
        this.$message.error("获取物流信息失败！");
      } else {
        this.wuliuList = res.data;
        this.wuliuVisible = true;
        // console.log(this.wuliuList);
      }
    },
    // 搜索框按钮
    shuContent(){
      alert(123)
    }
  }
};
</script>

