import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from'../components/Login.vue'
import Sy from'../components/Sy.vue'
import Wekcome from '../components/Wekcome.vue'
import Users from '../components/page/Users.vue'//用户列表
import Roles from '../components/page/Roles.vue'//角色列表
import Rights from '../components/page/Rights.vue'//权限列表
import Goods from '../components/page/Goods.vue'//商品列表
import Params from '../components/page/Params.vue'//分类参数
import Categories from '../components/page/Categories.vue'//商品分类
import Orders from '../components/page/Orders.vue'//订单列表
import Reports from '../components/page/Reports.vue'//数据报表
import Add from '../components/page/Add.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect:"/login"
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  //登录页面
  {
    path: '/login',
    name: 'Login',
    component: () => import('../components/Login.vue')
  },
  //登录成功后的页面
  {
    path: '/sy',
    name: 'Sy',
    component: () => import('../components/Sy.vue'),
    redirect:"/wekcome",
    children:[
      {
        path:"/wekcome",
        component: () => import('../components/Wekcome.vue'),
      },
      //用户列表
      {
        path:"/users",
        component: () => import('../components/page/Users.vue'),
      },
      //角色列表
      {
        path:"/roles",
        component: () => import('../components/page/Roles.vue'),
      },
      //权限列表
      {
        path:"/rights",
        component: () => import('../components/page/Rights.vue'),
      },
      //商品列表
      {
        path:"/goods",
        component: () => import('../components/page/Goods.vue'),
      },
      //分类参数
      {
        path:"/params",
        component: () => import('../components/page/Params.vue'),
      },
      //商品分类
      {
        path:"/categories",
        component: () => import('../components/page/Categories.vue'),
      },
      //订单列表
      {
        path:"/Orders",
        component: () => import('../components/page/Orders.vue'),
      },
      //数据报表
      {
        path:"/reports",
        component: () => import('../components/page/Reports.vue'),
      },
      {
        path:"/goods/add",
        component: () => import('../components/page/Add.vue'),
      },
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
