<template>
   <div class='rights'>
      <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>权限管理</el-breadcrumb-item>
            <el-breadcrumb-item>权限列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card> 
             <el-table
                :data="list"
                border stripe
                style="width: 100%">
                <el-table-column
                  label="#"
                   type ="index">
                </el-table-column>
                <el-table-column
                    prop="authName"
                    label="权限名称"
                    >
                </el-table-column>
                 <el-table-column
                    prop="path"
                    label="路径"
                  >
                </el-table-column>
                <el-table-column
                    label="权限等级"
                    >
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.level == 0">{{ scope.row.level | gl}}</el-tag>
                        <el-tag  v-if="scope.row.level == 1" type="success">{{ scope.row.level | gl}}</el-tag>
                        <el-tag  v-if="scope.row.level == 2" type="warning">{{ scope.row.level | gl}}</el-tag>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
          list:[]
       };
   },
   filters:{
      gl(val){
         if(val == "0"){
            return "一级"
         }else if(val == "1"){
             return "二级"
         }else if(val == "2"){
             return "三级"
         }
      }
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
      async getrightslist(){
           let { data:res } = await this.$http.get(`rights/list`)
           this.list = res.data
           console.log(this.list)
       }
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
       this.getrightslist()
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.el-table{
    margin-top: 20px;
}

</style>